* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
  padding: 0px;
  margin: 0px;
}
p,
h1,
h2 {
  margin-top: 0px;
  margin-bottom: 0px;
}
/* country-list {
} */
.country-list_item {
  display: flex;
  justify-content: start;
  align-items: center;
}
.country-list_img {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}
.country-card__wrapper {
  display: flex;
  justify-content: start;
  align-items: center;
}
